/* src/app/pages/boutique/boutique.scss */
main {
  background-color: #fff8e1;
  font-family: "Roboto", sans-serif;
}
.hero {
  height: 280px;
  display: flex;
  align-items: center;
  position: relative;
  background-color: rgba(245, 241, 224, 0.4941176471);
  padding-top: 8rem;
  padding-bottom: 8rem;
}
.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.hero h1,
.hero p {
  position: relative;
  z-index: 2;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}
.card {
  border: none;
  transition: transform 0.3s ease;
}
.card-img-top {
  height: 200px;
  object-fit: cover;
}
.text-success {
  color: #15803d !important;
}
.text-success:hover {
  color: #1ec962 !important;
}
.text-amber {
  color: #d97706 !important;
}
.btn-outline-warning {
  border-color: #d97706;
  color: #d97706;
}
.btn-outline-warning:hover {
  background-color: #d97706;
  color: #fff8e1;
}
.btn-warning {
  background-color: #d97706;
  border-color: #d97706;
}
.btn-warning:hover,
.hover-dark-green:hover {
  background-color: #14532d;
  border-color: #14532d;
}
.bg-light-amber {
  background-color: #fff8e1;
}
.text-dark {
  color: #333 !important;
}
.card:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.btn:hover {
  transform: scale(1.1);
  transition: transform 0.2s ease;
}
/*# sourceMappingURL=boutique.css.map */
